import React, { useState, createContext, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import FreindChallengeScoreCard from './FreindChallengeScoreCard';
import FriendChallengeName from './FriendChallengeName';
import FriendChallenger from '../FriendChallenges/FriendChallenger';
import FriendAttempterScore from './FriendAttempterScore';
import LoadingScreen from 'react-loading-screen';
import Header from '../Client/Header';
import besti from '../../Assets/besti.png';
export const DareChallengerContext = createContext();

function FriendLayout() {

    const params = useParams();

    const userName = localStorage.getItem("userNameChallenger") !== null ? localStorage.getItem("userNameChallenger") : localStorage.setItem("userNameChallenger", "") && "";
    const QuestionCompleted = localStorage.getItem("QuestionCompleted") !== null ? localStorage.getItem("QuestionCompleted") : localStorage.setItem("QuestionCompleted", false) && false;
    const newQuizData = localStorage.getItem("newQuizData") !== null ? localStorage.getItem("newQuizData") : localStorage.setItem("newQuizData", "") && "";
    const challengedSelected = localStorage.getItem("challengedSelected");

    const [naviagation, setNavigation] = useState({ "userNameChallenger": userName, "challengedSelected": challengedSelected, "newQuizData": newQuizData });
    const [isLoading, SetisLoading] = useState(false);


    useEffect(() => {
        if (userName) {
            setNavigation({ ...naviagation, "userNameChallenger": userName, "challengedSelected": challengedSelected, "QuestionCompleted": QuestionCompleted, "newQuizData": newQuizData })
        }
    }, [userName, challengedSelected, QuestionCompleted, newQuizData]);

    const handleUserClick = (e) => {

        if (e.hasOwnProperty("userNameChallenger")) {
            setNavigation({ ...naviagation, "userNameChallenger": e.userName })
        }

        if (e.hasOwnProperty("QuestionCompleted")) {
            setNavigation({ ...naviagation, "QuestionCompleted": e.QuestionCompleted })
            SetisLoading(true);
            setTimeout(() => {
                SetisLoading(false);
            }, 1500);
        }
    }
    const stringToBoolean = (string) => { return string === 'false' ? false : !!string };

    if (stringToBoolean(naviagation.challengedSelected)) {
        return <>
            <DareChallengerContext.Provider value={{ naviagation, handleUserClick, params }}>
                {naviagation.challengedSelected && <FreindChallengeScoreCard />}
            </DareChallengerContext.Provider>
        </>
    }
    if (!!params.challengeId) {
        return <>
            <DareChallengerContext.Provider value={{ naviagation, handleUserClick, params }}>
                <LoadingScreen
                    loading={isLoading}
                    bgColor='#1c1b1b'
                    spinnerColor='#FFFFFF'
                    textColor='#FFFFFF'
                    logoSrc={besti}
                    text='Please Wait! We are Calculating your Score.'>
                    <Header />
                    {!naviagation.userNameChallenger && !naviagation.QuestionCompleted && <FriendChallengeName />}
                    {!!naviagation.userNameChallenger && naviagation.QuestionCompleted && !naviagation.newQuizData && <FriendChallenger />}
                    {!!naviagation.userNameChallenger && naviagation.QuestionCompleted && !!naviagation.newQuizData && <FriendAttempterScore />}
                </LoadingScreen>
            </DareChallengerContext.Provider >
        </>
    }

}
export default FriendLayout;