import React, { useState, createContext, useEffect } from 'react';
import Header from './Header';
import Challengecard from './Challengecard';
import CardContainer from './CardContainer';
import StartCardData from './startCard';
import DareSharedQuizCard from './daresharedquizcard';
import LoadingScreen from 'react-loading-screen';
import besti from '../../Assets/besti.png';
import { useNavigate } from 'react-router';

export const DareClientContext = createContext();

function LayoutClient() {
    const navigate = useNavigate();
    const challengedSelected = localStorage.getItem("challengedSelected") !== null ? localStorage.getItem("challengedSelected") : localStorage.setItem("challengedSelected", false) && false;
    const userName = localStorage.getItem("userName") !== null ? localStorage.getItem("userName") : localStorage.setItem("userName", "") && "";
    const QuestionCompleted = localStorage.getItem("QuestionCompleted") !== null ? localStorage.getItem("QuestionCompleted") : localStorage.setItem("QuestionCompleted", false) && false;
    const newQuizData = localStorage.getItem("newQuizData") !== null ? localStorage.getItem("newQuizData") : localStorage.setItem("newQuizData", "") && "";

    const [naviagation, setNavigation] = useState({});
    const [isLoading, SetisLoading] = useState(false);

    useEffect(() => {
        setNavigation({
            ...naviagation,
            "challengedSelected": stringToBoolean(challengedSelected),
            "userName": userName,
            "QuestionCompleted": stringToBoolean(QuestionCompleted),
            "newQuizData": newQuizData
        })
    }, [challengedSelected, userName, QuestionCompleted, newQuizData]);

    const handleUserClick = (e) => {
        if (e.hasOwnProperty("challengedSelected")) {
            setNavigation({ ...naviagation, "challengedSelected": e.challengedSelected })
            localStorage.setItem("challengedSelected", e.challengedSelected)
            navigate('/en/')
        }

        if (e.hasOwnProperty("userName")) {
            setNavigation({ ...naviagation, "userName": e.userName });
            localStorage.setItem("userName", e.userName);
        }

        if (e.hasOwnProperty("QuestionCompleted")) {
            setNavigation({ ...naviagation, "QuestionCompleted": e.QuestionCompleted, "newQuizData": e.newQuizData })
            SetisLoading(true);
            setTimeout(() => {
                SetisLoading(false);
            }, 1000);
        }
    }

    const stringToBoolean = (string) => { return string === 'false' ? false : !!string };

    return (<>
        <DareClientContext.Provider value={{ naviagation, handleUserClick }}>
        <LoadingScreen
                    loading={isLoading}
                    bgColor='#1c1b1b'
                    spinnerColor='#FFFFFF'
                    textColor='#FFFFFF'
                    logoSrc={besti}
                    text='Please Wait! your quiz is being creating.'>
                <Header />
                {!stringToBoolean(naviagation.challengedSelected) && <Challengecard />}
                {stringToBoolean(naviagation.challengedSelected) && !stringToBoolean(naviagation.userName) && <StartCardData />}
                {stringToBoolean(naviagation.challengedSelected) && !!stringToBoolean(naviagation.userName) && !stringToBoolean(naviagation.QuestionCompleted) && <CardContainer />}
                {stringToBoolean(naviagation.challengedSelected) && !!stringToBoolean(naviagation.userName) && stringToBoolean(naviagation.QuestionCompleted) && stringToBoolean(naviagation.newQuizData) && <DareSharedQuizCard />}
            </LoadingScreen>
        </DareClientContext.Provider>
    </>
    )
}

export default LayoutClient;