
import truth from '../../Assets/truth.png';
import { useContext, useState } from 'react';
import { DareAdminContext } from './AdminLayout';


function AccountSettings() {
    const { token, handleAdminClick } = useContext(DareAdminContext);
    const FetchToken = JSON.parse(token);
    const [inputs, setInputs] = useState({});

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setInputs(values => ({ ...values, [name]: value }))
    }
    const handleSubmit = (event) => {
        event.preventDefault();
        if (!inputs.username && !inputs.password) {
            fetchQuiz().then((e) => {
                handleAdminClick({ "passwordupdate": true })
            }).catch()
        }

    }
    const fetchQuiz = async (e) => {
        const response = await  fetch(`${process.env.REACT_APP_BASE_URL}/updateAdmin`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": FetchToken.accessToken
            },
            body: JSON.stringify(inputs)
        })
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }
    return (
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <h1 className="mt-5">Account Settings</h1>
                    <p className="lead">
                        <form method="post" onSubmit={handleSubmit} className="form-horizontal" >

                            <div className="form-group">
                                <label htmlFor="username">Username</label>
                                <input type="text" className="form-control" name="username" id="username" placeholder="Enter new username" onChange={handleChange} value={inputs.Username} />
                            </div>

                            <div className="form-group">
                                <label htmlFor="password">Password</label>
                                <input type="password" className="form-control" name="password" id="password" placeholder="Enter new password" onChange={handleChange} value={inputs.Password} />
                            </div>

                            <button type="submit" disabled={!inputs.username || !inputs.password} className="btn btn-success float-right">Update</button>
                        </form>
                    </p>
                </div>
                <div className="col-md-4">
                    Site OG image
                    <img className="img-thumbnail img-fluid mt-5" src={truth} alt='sdfsd' />
                </div>
            </div>
        </div>
    );
}

export default AccountSettings;