import { useContext, useEffect, useState } from 'react';
import { DareAdminContext } from './AdminLayout';
import QuizListTable from './QuizListTable'
function Dashboard() {
    const { token } = useContext(DareAdminContext);
    const FetchToken = JSON.parse(token);
    const [quizList, SetQuizList] = useState([]);
    const getAllQuiz = async () => {
        const response = await  fetch(`${process.env.REACT_APP_BASE_URL}/getAllQuiz`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": FetchToken.accessToken
            }
        })
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        getAllQuiz().then(e => {
            SetQuizList(e)
        })
    }, [])
    return (
        <div className="container my-3">
            <div className="row">
                <div className="col-md-4">
                    <div className="card text-center" >
                        <div className="card-body">
                            <h2>{quizList.totalQuestions}</h2>
                        </div>
                        <div className="card-header fw-bold">
                            Total Questions
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="card text-center" >
                        <div className="card-body">
                            <h2>{quizList.totalQuiz}</h2>
                        </div>
                        <div className="card-header fw-bold ">
                            Total Quiz
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="card text-center" >
                        <div className="card-body">
                            <h2>{quizList.totalView}</h2>
                        </div>
                        <div className="card-header fw-bold ">
                            Total Quiz View
                        </div>
                    </div>
                </div>
            </div>
            {quizList?.allQuizs && <QuizListTable props={quizList?.allQuizs} />}
        </div>
    );
}

export default Dashboard;




