import { useContext } from 'react';
import { DareAdminContext } from './AdminLayout'
import { ToastContainer, toast } from 'react-toastify';



function SignIn() {
    
    const { handleAdminClick } = useContext(DareAdminContext);
    const fetchData = async (username, password) => {
        const response = await  fetch(`${process.env.REACT_APP_BASE_URL}/login`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: (JSON.stringify({
                "username": username,
                "password": password
            }))
        })
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }
    const onSumitSignForm = (e) => {
        e.preventDefault();
        if (e.target.elements.username.value && e.target.elements.password.value) {
            fetchData(e.target.elements.username.value, e.target.elements.password.value).then((data) => {
                if (data.message === "Login Successfull.") {
                    handleAdminClick({"SignIn" : JSON.stringify(data)})
                }
                toast(data.message)
            }).catch();
        }
    }
    return (
        <div className="position-relative h-100vh">
                <div className="card adminCard">
                    <div className="my-card-header card-header-success text-center">
                        <h4>Sign In</h4>
                    </div>
                    <div className="p-3">
                        <form method="post" onSubmit={onSumitSignForm}>

                            <div className="input-group mb-3">
                                <input type="text" className="form-control" placeholder="Username" name="username" required="" />
                            </div>

                            <div className="input-group mb-3">
                                <input type="password" className="form-control" placeholder="pasword" name="password" required="" />
                            </div>

                            <div className="text-center mt-4">
                                <button className="btn btn-success" type="submit">Login</button>
                            </div>
                        </form>

                    </div>
                </div>
            <ToastContainer />
        </div>

    );
}

export default SignIn;




