import { useContext, useEffect, useState } from 'react';
import { DareAdminContext } from './AdminLayout';
import { ToastContainer, toast } from 'react-toastify';
function QuizListTable(props) {
    const { token } = useContext(DareAdminContext);
    const [quizList, SetQuizList] = useState(props?.props);
    const [filteredData, setFilterData] = useState(props?.props);
    const FetchToken = JSON.parse(token)

    const DelQuizItem = async (quiz_Id) => {
        const response = await (await fetch(`${process.env.REACT_APP_BASE_URL}/deleteQuiz/${quiz_Id}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": FetchToken.accessToken
            }
        }))
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }
    const filterTable = (e) => {
        const searchText = e.target.value.toLowerCase();
        let searchData = quizList.filter(emp => emp.quiz_performer.toLowerCase().includes(searchText));
        setFilterData(searchData)
    }
    useEffect(() => {
        SetQuizList(props?.props)
        setFilterData(props?.props)
    }, [quizList])

    const deleteQuiz = (e) => {
        DelQuizItem(e).then(ea => {
            toast(`Quiz ID "${e}" Deleted Successfully`)
            window.location.reload(); 
        }
        ).catch((eas) => {
            console.log(eas)
        })
    }

    return (
        <div className="container my-3">
            <div className="align-items-center d-flex justify-content-between mt-5">
                <h1>Quiz List</h1>
                <div className="dataTables_filter">
                    <label>Search:<input type="search" className="form-control form-control-sm" placeholder="" disabled={quizList.length === 0} onChange={filterTable} /></label></div>
            </div>
            <hr />
            <table className="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Quiz Id</th>
                        <th scope="col">Performer</th>
                        <th scope="col">Views</th>
                        <th scope="col">Action</th>
                    </tr>
                </thead>
                <tbody>
                    {filteredData && filteredData.map((e, i) => {
                        return <tr key={i}>
                            <th>{i + 1}</th>
                            <td> {e.quiz_uid}</td>
                            <td> {e.quiz_performer}</td>
                            <td> {e.quiz_view}</td>
                            <td> <button className=' btn btn-danger text-capitalize' onClick={() => deleteQuiz(e.quiz_uid)}> Del</button> </td>
                        </tr>
                    })
                    }
                </tbody>
            </table>
            <ToastContainer />
        </div>
    );
}

export default QuizListTable;
