import { useContext, useEffect, useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { DareAdminContext } from './AdminLayout';

function QuestionList() {
    const { token, handleAdminClick } = useContext(DareAdminContext);
    const [quizList, SetQuizList] = useState([]);
    const [quiz, SetQuiz] = useState([]);
    const FetchToken = JSON.parse(token);
    const [ShowViewAnswer, setShowViewAnswer] = useState(false);

    useEffect(() => {
        fetchQuizListData().then(e => {
            SetQuizList(e)
        })
    }, [])

    const handleCloseView = () => setShowViewAnswer(false);
    const handleShowView = () => setShowViewAnswer(true);
    const [ShowListAnswer, setShowListAnswer] = useState([]);

    const ViewQuestion = (e) => {
        fetchQuiz(e.q_id).then((e) => {
            setShowListAnswer(e);
            handleShowView()
        }).catch((ea) => {console.log(ea)})
    }

    const DeleteQuestion = (e) => {
        try {
            fetch(`${process.env.REACT_APP_BASE_URL}/deleteQuestionById/${e.q_id}`,
                { method: "GET", headers: { Authorization: FetchToken.accessToken } }
            ).then((res) => {
                if (res?.statusText === "OK"){
                 handleAdminClick({ "deleteQuestionById": true })
                }
            });
        } catch (error) {
            console.error("Error:", error);
        }
    }

    const fetchQuizListData = async () => {
        const response = await (await fetch(`${process.env.REACT_APP_BASE_URL}/getQuestions`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": FetchToken.accessToken
            }
        }))
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }

    const filterTable = (e) => {
        const searchText = e.target.value.toLowerCase();
        let searchData = quizList.filter(emp => emp.performer.toLowerCase().includes(searchText.toLowerCase()));
        SetQuizList(searchData)
    }

    const [ShowEditAnswer, setShowEditAnswer] = useState();
    const handleCloseEdit = () => setShowEditAnswer(false);
    const handleShowEdit = () => setShowEditAnswer(true);
    const [options, setOptions] = useState([]);

    const EditQuestion = (e) => {
        fetchQuiz(e.q_id).then((e) => {
            setOptions(e.Answers)
        }).catch((ea) => { console.log(ea) })
        SetQuiz(e)
        handleShowEdit()
    }

    const fetchQuiz = async (e) => {
        const response = await  fetch(`${process.env.REACT_APP_BASE_URL}/getQuestionWithAnswerById/${e}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": FetchToken.accessToken
            }
        })
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }

    const handleOptionChange = (e, optionIndex) => {
        const updatedOptions = [...options];
        updatedOptions[optionIndex].a_text = e.target.value;
        setOptions(updatedOptions);
    };

    const handleImageChange = (e, optionIndex) => {
        const file = e.target.files[0];
        if (file) {
            const updatedOptions = [...options];
            updatedOptions[optionIndex].img = file;
            setOptions(updatedOptions);
        }
    };

    const handleSubmitUpdateAnser = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        options.forEach((option) => {
            formData.append(`options[${option.a_id}][a_text]`, option.a_text);
            formData.append(`options[${option.a_id}][a_thumb]`, option.a_thumb);
            formData.append(`options[${option.a_id}][a_image]`, option.img);
        });

        try {
            await fetch(
                `${process.env.REACT_APP_BASE_URL}/updateQuestionWithAnswers`,
                {
                    method: "POST",
                    headers: {
                        Authorization: FetchToken.accessToken,
                    },
                    body: formData,
                }
            ).then((e) => {
                handleCloseEdit()
                handleAdminClick({ "updateAnswerById": true })
            });
        } catch (error) {
            console.error("Error:", error);
        }
    };

    return (
        <div className="container my-3">
            <div>
                <Modal
                    show={ShowEditAnswer}
                    onHide={handleCloseEdit}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title><p> {quiz.q_title}</p> </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        {ShowEditAnswer && options?.map((x, i) => {
                            return (
                                <div key={i}>
                                    <div className='d-flex justify-content-between'>
                                        <h5>Answer #{i + 1}</h5>
                                        <img className='answerlist-img-edit col-4 mb-3' alt={x.a_thumb} src={`${process.env.REACT_APP_BASE_URL}/public/${x.a_thumb}`} />
                                    </div>
                                    <div className="d-flex justify-content-between" key={i}>
                                        <div className="container">
                                            <label htmlFor="Answer">Enter Answer</label>
                                            <input className="form-control"
                                                name="a_text"
                                                placeholder="Enter Answer"
                                                value={x.a_text}
                                                onChange={(e) => handleOptionChange(e, i)}
                                            />
                                            <div className="form-group mt-3">

                                                <label htmlFor="picturePath">Select Image For answer</label>
                                                <input type="file" name="img" className="form-control" onChange={(e) => handleImageChange(e, i)} placeholder="select image" required="{true}" />
                                                <span className="text-danger"> Image must be 200X200 pixel in size</span>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            );
                        })}


                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleCloseEdit}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={handleSubmitUpdateAnser}>
                            Save
                        </Button>
                    </Modal.Footer>
                </Modal>


            </div>
            <div className="align-items-center d-flex justify-content-between mt-5">
                <h1>Question List</h1>
                <div className="dataTables_filter">
                    <label>Search:<input type="search" className="form-control form-control-sm" placeholder="" onChange={filterTable} /></label></div>
            </div>
            <hr />
            <table className="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Question Title</th>
                        <th scope="col">Answer</th>
                        <th scope="col">Publish</th>
                        <th scope="col">Option</th>
                    </tr>
                </thead>
                <tbody>
                    {quizList && quizList.map((e, i) => (
                        <tr key={i}>
                            <th>{i + 1}</th>
                            <td> {e.q_title}</td>
                            <td> <button className='btn btn-success' onClick={() => { ViewQuestion(e) }} value={e.q_id} > View</button></td>
                            <td className='text-center'> {e.q_status === 1 ? 'Yes' : 'No'}</td>
                            <td> <button onClick={() => { EditQuestion(e) }} value={e.q_id} className=' btn bg-secondary text-white text-capitalize me-2'> Edit</button>
                                <button onClick={() => { DeleteQuestion(e) }} value={e.q_id} className=' btn btn-danger text-capitalize'> Delete</button></td>
                        </tr>))
                    }
                </tbody>
            </table>
            <div>
                <Modal
                    show={ShowViewAnswer}
                    onHide={handleCloseView}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title><p> {ShowListAnswer?.q_title}</p> </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className='row'>
                            {ShowListAnswer && ShowListAnswer?.Answers?.map((e, i) => {
                                return <>
                                    <p className='col-8'>{e?.a_text}</p>
                                    <img className='answerlist-img col-4 mb-3' alt={e.a_thumb} src={`${process.env.REACT_APP_BASE_URL}/public/${e.a_thumb}`} />
                                </>
                            })
                            }
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleCloseView}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>
        </div>
    );
}

export default QuestionList;
