
import { useContext } from 'react';
import besti from '../../Assets/besti.png';
import dare from '../../Assets/dare.png';
import twentythree from '../../Assets/2023.png';
import truth from '../../Assets/truth.png';
import { DareClientContext } from './LayoutClient'
function Challengecard() {

    const { handleUserClick } = useContext(DareClientContext);

    const handleChallengeClick = (event) => {
        handleUserClick({ "challengedSelected": true })
    };
    return (<>
        <div className="container">
            <div className="offset-md-2 col-md-8">

                <div className="dare_card_container box" onClick={()=>handleChallengeClick(1)}>
                    <img src={besti} alt="Besti" className="max-width-100"  />
                    <h3 className="">
                        Friendship Dare {new Date().getFullYear()}
                    </h3>
                    <button className="bg-danger border-radius-0 text-white" value={1} >
                        Start
                    </button>
                </div>

                <div className="dare_card_container box" onClick={()=>handleChallengeClick(2)}>
                    <img src={dare} alt="Besti" className="max-width-100"  />
                    <h3 className="">Friendship Meter</h3>

                    <button className="bg-danger border-radius-0 text-white " >
                        Start
                    </button>
                </div>

                <div className="dare_card_container box" onClick={()=>handleChallengeClick(3)}>
                    <img src={twentythree} alt="Besti" className="max-width-100"  />
                    <h3 className="">
                        Friends Dare Challenge {new Date().getFullYear()}
                    </h3>

                    <button className="bg-danger border-radius-0 text-white" >
                        Start
                    </button>

                </div>
                <div className="dare_card_container box" onClick={()=>handleChallengeClick(4)}>
                    <img src={truth} alt="Besti" className="max-width-100"  />

                    <h3 className="">
                        TRUTH or FALSE
                    </h3>

                    <button className="bg-danger border-radius-0 text-white" >
                        Start
                    </button>

                </div>
            </div>
        </div>
    </>
    );
}

export default Challengecard;