import React, { useContext, useRef, useEffect, useState } from 'react';
import { DareChallengerContext } from './FriendLayout'
import { ToastContainer, toast } from 'react-toastify';
import { useNavigate } from "react-router-dom";
import twentythree from '../../Assets/2023.png';

function FriendChallengeName() {

    const inputhandleUserNameRef = useRef(null);
    const { params, handleUserClick } = useContext(DareChallengerContext);
    const [userName, SetUsername] = useState();
    const [getScored, setGetScroed] = useState([]);
    const navigate = useNavigate();
    const handleStartCardEventFn = (e) => {
        if (!!inputhandleUserNameRef.current.value) {
            handleUserClick({ "userNameChallenger": inputhandleUserNameRef.current.value })
            localStorage.setItem("userNameChallenger", inputhandleUserNameRef.current.value)
        }
        else {
            toast("User Name is required")
        }
    };
    const fetchData = async () => {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getAllChallengerByQuizId/${params.challengeId}`)
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        fetchData()
            .then((res) => {
                setGetScroed(res.quiz_performer)
                SetUsername(res.quiz_owner)
            })
            .catch((e) => {
                navigate('/');
                toast("Question is delted by owner")
                window.location.reload();
               
            })
    }, []);


    return (
        <div className="container">
            <div className=" col-md-6  offset-md-3">
                <div className="card mt-3">
                    <div className="bg-light card-body border-1-black shadow text-center">
                        <img src={twentythree} className='max-width-150' alt="challenge header img" />
                        <h4>Friendship Dare {new Date().getFullYear()}</h4>
                        <p>How Well You Really Know {userName}</p>
                        <div className="input-group mb-3">
                            <span className="input-group-text"><svg style={{width: '20px'}} viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><defs></defs><title/><g data-name="Layer 7" id="Layer_7"><path className="cls-1" d="M19.75,15.67a6,6,0,1,0-7.51,0A11,11,0,0,0,5,26v1H27V26A11,11,0,0,0,19.75,15.67ZM12,11a4,4,0,1,1,4,4A4,4,0,0,1,12,11ZM7.06,25a9,9,0,0,1,17.89,0Z"/></g></svg></span>
                            <input minLength={3} maxLength={20} ref={inputhandleUserNameRef} type="text" className="form-control" placeholder="Enter your full name" />
                        </div>
                        <div>
                            <button className="bg-danger btn text-white border-radius-100 p-2" onClick={handleStartCardEventFn}>Start</button>
                        </div>
                        <div className='my-3'>
                            <h5>Who Knows {userName} Best?</h5>
                            <div className="text-center">
                                <table className="table table-bordered table-striped">
                                    <thead >
                                        <tr>
                                            <th className="bg-danger text-white">Name</th>
                                            <th className="bg-danger text-white">Score</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {getScored && getScored?.map((x, i) => {
                                            return (<tr key={i}>
                                                <td className='text-capitalize'>
                                                    {x.c_name}
                                                </td>
                                                <td>
                                                    {x.c_score}
                                                </td>
                                            </tr>)

                                        })}
                                    </tbody>
                                </table>
                            </div>
                        </div>

                        <a href={window.location.origin} onClick={() => { localStorage.clear() }} className="btn btn-warning mt-2 p-1">Create Your Own Quiz</a>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>

    );
}
export default FriendChallengeName;