
import React, { useState, createContext, useEffect } from 'react';
import { Routes, Route, useNavigate } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import AccountSettings from './AccountSetting';
import Dashboard from './Dashboard';
import Header from './Header';
import SiteSettings from './SiteSettings';
import QuestionForm from './QuestionForm';
import SignIn from './SignIn';
import QuestionList from './QuestionList'
import NoPage from '../NoPage';
import besti from '../../Assets/besti.png';
import LoadingScreen from 'react-loading-screen';
export const DareAdminContext = createContext();
function AdminLayout() {
    const [isLoading, SetisLoading] = useState(false);
    const navigate = useNavigate();
    const handleAdminClick = (e) => {
        SetisLoading(true);
        setTimeout(() => {
            SetisLoading(false);
        }, 1500);
        if (e.hasOwnProperty("SignIn")) {
            setToken(e.SignIn)
            localStorage.setItem("SignIn", JSON.stringify(e.SignIn))
            toast(`Login Successfully`);
            navigate("/Admin/");
        }
        if (e.hasOwnProperty("newQuestionCreated"))
            toast(`Question Added Successfully`);
            navigate('/Admin/')

        if (e.hasOwnProperty("updateAnswerById")) {
            toast(`Question Updated Successfully`);
            window.location.reload()
        }
        if (e.hasOwnProperty("deleteQuestionById")) {
            toast(`Question Deleted Successfully`);
            window.location.reload()
        }
        if (e.hasOwnProperty("passwordupdate")) {
            toast(`Password Updated Successfully`);
            navigate('/Admin/')
        }
        if (e.hasOwnProperty("siteDaataUpdate")) {
            toast(`Site Settings Updated Successfully`);
            navigate('/Admin/')
        }

    }

    const [token, setToken] = useState();
    useEffect(() => {
        setToken(JSON.parse(localStorage.getItem('SignIn')))
    }, [token])

    if (!token) { return <DareAdminContext.Provider value={{ handleAdminClick }}><SignIn /> </DareAdminContext.Provider > }

    return (<>
        <DareAdminContext.Provider value={{ token, handleAdminClick }}>
            <LoadingScreen
                loading={isLoading}
                bgColor='#f1f1f1'
                spinnerColor='#9ee5f8'
                textColor='#676767'
                logoSrc={besti}
                text='Please Wait! We are Processing.'>
                <Header />
                <Routes>
                    <Route path="/" element={<Dashboard />} />
                    <Route path="accountssettings" element={<AccountSettings />} />
                    <Route path="questionform" element={<QuestionForm />} />
                    <Route path="sitesettings" element={<SiteSettings />} />
                    <Route path="questionlist" element={<QuestionList />} />
                    <Route path="*" element={<NoPage />} />
                </Routes>
            </LoadingScreen>
        </DareAdminContext.Provider>
        <ToastContainer />
    </>
    );

}
export default AdminLayout;
