import React, { useContext, useCallback, useEffect, useState } from 'react';
import { DareChallengerContext } from './FriendLayout'
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { ToastContainer, toast } from 'react-toastify';
import twentythree from '../../Assets/2023.png';
import stop from '../../Assets/stop.png';
import whatsapp from '../../Assets/whatsapp-logo.png';




function FreindChallengeScoreCard() {
    const { params } = useContext(DareChallengerContext);
    const [copied, setCopied] = useState(false);
    const [locations, setlocation] = useState("");
    const [getScored, setGetScroed] = useState([]);
    const [AdURL, SetAdURL] = useState("");
    const [siteData, setSiteData] = useState([]);

    const onCopy = useCallback(() => {
        setCopied(true);
        toast("Copied")
    }, [])

    const DeleteUserData = async () => {
        DelQuizItem().then(e => {
            toast(`Quiz ID "${e}" Deleted Successfully`)
            window.location.reload()
        }
        ).catch((eas) => {
            console.log(eas)
        })
    }
    const DelQuizItem = async () => {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/deleteQuiz/${params.challengeId}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            }
        })
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            localStorage.clear();
            window.location.reload()
            return response.json()
        }

    }

    useEffect(() => {
        const siteDataLocal =JSON.parse( localStorage.getItem("siteData"));
        setSiteData(siteDataLocal[0]);  
        async function fetchData() {
            try {
                const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getAllChallengerByQuizId/${params.challengeId}`);
                if (!response.ok) {
                    throw new Error('Request failed with status ' + response.status);
                }
                const jsonData = await response.json();
                setGetScroed(jsonData)
                setlocation(`${window.location.origin + '/en/quiz/' + params.challengeId}`)
                SetAdURL(jsonData.advertisementURL)

            } catch (error) {
                console.log(error)
            }
        }
        fetchData();
    }, []);

    const createOwnQuiz = () => {
        localStorage.setItem("QuestionCompleted", false)
        localStorage.setItem("FriendchallengedSelected", false)
        localStorage.setItem("challengedSelected", false)
        localStorage.setItem("userName", "")
        localStorage.setItem("userNameChallenger", "")
    }

    return (
        <div className="container">
            <div className="col-md-6 offset-md-3 mt-5">
                <div className="card mt-3">
                    <div className="card-body p-4 shadow text-center border-1-black">
                        <img src={twentythree} className='max-width-150' alt="challenge header img" />

                        {getScored.length !== 0 && <>
                            <h4>Your Challenge is Ready</h4>
                            <p>Share this link with your friends</p>
                            <div className="row">

                                <div className='col-lg-8 col-sm-12 px-0 mb-2'>
                                    <input className="border btn shadow w-100" type="input" readOnly value={locations} />
                                </div>

                                <div className='col-lg-4 col-sm-12 px-0'>
                                    <CopyToClipboard onCopy={onCopy} text={locations}>
                                        <button className="bg-primary btn  text-white w-100 copy-btn">Copy This Link</button>
                                    </CopyToClipboard>
                                </div>

                                <div className="col-md-12 mt-2">
                                    <a className="btn btn-success border-radius-100 mb-2 w-100" href={`whatsapp://send?text= ${getScored ?  getScored.quiz_owner : ''} ${siteData?.site_description } ${locations}`} target="_blank">
                                    <img src={whatsapp} alt="React Logo" />

                                        <span className="ms-1">Send to WhatsApp </span>
                                    </a>
                                </div>
                            </div> </>
                        }

                        {getScored.length == 0 && <div>
                            <img className='max-width-150 my-2' src={stop} />
                            <h6>The Quiz has been deleted by the owner. You can create your own by clicking the below button.</h6>
                        </div>}

                        <div className="mt-3">
                            <br />
                            <a href={AdURL} target="_blank">
                                Touch this blue line and see magic
                            </a>
                            <br />
                        </div>
                        {getScored.length !== 0 && <>
                            <div className="mt-3">
                                <button className='bg-warning btn' onClick={DeleteUserData}>You can delete this quiz by clicking here</button>
                            </div>
                        </>}

                        {getScored.length == 0 && <div>
                            <a href={window.location.origin} onClick={createOwnQuiz} className="btn btn-warning mt-2 p-1">Create Your Own Quiz</a>
                        </div>}
                    </div>
                </div>


                {getScored.length !== 0 && <>
                    <div className="mt-5 text-center">

                        <h5>Who Knows you Best?</h5>
                        <table className="table table-bordered table-striped">
                            <thead >
                                <tr>
                                    <th className="bg-danger text-white">Name</th>
                                    <th className="bg-danger text-white">Score</th>
                                </tr>
                            </thead>
                            <tbody>

                                {getScored && getScored?.quiz_performer?.map((x, i) => {
                                    return (<tr key={i} >
                                        <td className='text-capitalize'>
                                            {x.c_name}
                                        </td>
                                        <td>
                                            {x.c_score}
                                        </td>
                                    </tr>)

                                })}
                            </tbody>
                        </table>
                        <ToastContainer />

                    </div>
                </>}
            </div>
        </div>
    );
}

export default FreindChallengeScoreCard;