import React from 'react';
import { Route, Routes, Navigate } from 'react-router-dom';
import './App.css';
import AdminLayout from './components/Admin/AdminLayout';
import NoPage from './components/NoPage';
import LayoutClient from './components/Client/LayoutClient';
import FriendLayout from './components/FriendChallenges/FriendLayout';
import twentythree from './Assets/2023.png';
import { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { getFirebaseToken} from './firebase';

export default function App() {
  const [inputs, setInputs] = useState({});
  const fetchSiteData = async () => {
    const response = await (await fetch(`${process.env.REACT_APP_BASE_URL}/getsite`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    }))
    if (!response.ok) {
      throw new Error('Data coud not be fetched!')
    } else {
      return response.json()
    }
  }

  useEffect(() => {
    fetchSiteData().then(e => {
      setInputs(e[0])
      localStorage.setItem("siteData", JSON.stringify(e))
    })
    
    let theHeader = new Headers(); theHeader.append("Content-Type", "application/json");
    const REACT_APP_BASE_URL_NOTIFICATION = "https://new.pushit.in/api"; 
    if (!localStorage.getItem("tokenSent")) {
      getFirebaseToken()
        .then((firebaseToken) => {
          if (firebaseToken) {
            const body = {
              sitename: window.location.hostname, country: "India", token: firebaseToken,
            }; fetch(`${REACT_APP_BASE_URL_NOTIFICATION}/user/subscribe`,
              {
                method: "POST", headers: theHeader, body: JSON.stringify(body),
              }).then((r) => {
                localStorage.setItem("tokenSent", true); r.json(() => { });
              });
          }
        })
        .catch((err) => console.error('An error occured while retrieving firebase token. ', err))
    }

  }, []);


  return (
    <div>
      <Helmet>
        <title>{inputs?.site_title}</title>
        <link
          rel="icon" sizes="16x16"
          type="image/png" // Adjust the image type if needed (e.g., image/x-icon for .ico files)
          href={twentythree}
        />

        <meta name="description" content={inputs?.site_title} />
        <meta property="og:image" content={process.env.REACT_APP_BASE_URL + '/' + inputs?.site_og_image}></meta>
      </Helmet>
      <Routes>
        <Route exact path="/" element={<Navigate to="en/" replace />} />
        <Route index path="en/" element={<LayoutClient />} />
        <Route exact path="en/quiz/:challengeId" element={<FriendLayout />} />
        <Route exact path="admin/*" element={<AdminLayout />} />
        <Route path="*" element={<NoPage />} />
      </Routes>
    </div>
  );
}
