import { Link } from "react-router-dom";
function Header() {
    const logout = () => {
        localStorage.removeItem("SignIn");
        window.location.reload();
    };
    return (
        <>
            <header className="App-header ">
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                    <div className="container-fluid">
                        <p className="navbar-brand mb-0">Admin</p>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarText">
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                                <li className="nav-item">
                                    <li className="nav-link active" aria-current="page" ><Link to="/admin">Home</Link></li>
                                </li>
                                <li className="nav-item dropdown">
                                    <li className="nav-link dropdown-toggle" id="navbarDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Question
                                    </li>
                                    <ul className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                        <Link to="questionform"><li className="dropdown-item">Add New</li></Link>
                                        <Link to="questionlist"><li className="dropdown-item">View</li></Link>
                                    </ul>
                                </li>
                                <li className="nav-item dropdown">
                                    <li className="nav-link dropdown-toggle" id="navbarDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Settings
                                    </li>
                                    <ul className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                        <Link to="sitesettings"><li className="dropdown-item">Site Settings</li></Link>
                                        <Link to="accountssettings"><li className="dropdown-item">Account Settings</li></Link>
                                    </ul>
                                </li>
                            </ul>
                            <button className="btn btn-outline-danger" onClick={logout}>
                                Logout
                            </button>
                        </div>
                    </div>
                </nav>
            </header>


        </>
    )
}
export default Header
