import React, { useContext, useEffect, useState } from 'react';
import PieChart from 'react-simple-pie-chart';
import twentythree from '../../Assets/2023.png';
import { DareChallengerContext } from './FriendLayout'


function FriendAttempterScore() {

    const { params, naviagation } = useContext(DareChallengerContext);
    const [userName, SetUsername] = useState();
    const [getScored, setGetScroed] = useState([]);

    const fetchData = async () => {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getAllChallengerByQuizId/${params.challengeId}`)
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        fetchData()
            .then((res) => {
                setGetScroed(res.quiz_performer)
                SetUsername(res.quiz_owner)
            })
            .catch()
    }, []);

    const createOwnQuiz = () => {
        localStorage.setItem("QuestionCompleted", false)
        localStorage.setItem("FriendchallengedSelected", false)
        localStorage.setItem("challengedSelected", false)
        localStorage.setItem("userName", "")
        localStorage.setItem("userNameChallenger", "")
        
    }
    return (
        <div className="container">
            <div className="col-md-6 offset-md-3 mt-5">
                <div className="card mt-3">
                    <div className="card-body p-4 shadow text-center border-1-black">
                        <img src={twentythree} className='max-width-150' alt="challenge header img" />
                        <h4>Friendship Dare {new Date().getFullYear()}</h4>
                        <div className=" text-center">
                            <div className='pieChart-attempter'>
                                {getScored && getScored.length !==0 && <div >
                                    <PieChart
                                        slices={[
                                            {
                                                color: '#198754',
                                                value: getScored?.filter(x => x.c_name.toLowerCase() === naviagation?.userNameChallenger.toLowerCase())[0]?.c_score,
                                            },
                                            {
                                                color: '#f00',
                                                value: 20 - getScored?.filter(x => x.c_name.toLowerCase() === naviagation?.userNameChallenger.toLowerCase())[0]?.c_score,
                                            },
                                        ]}
                                    />
                                    <span className='pieChartScore'>
                                        {getScored?.filter(x => x.c_name.toLowerCase() === naviagation?.userNameChallenger.toLowerCase())[0]?.c_score}/{20}
                                    </span>
                                </div>
                                }

                            </div>

                            <h5 className='my-2'>Who Knows {userName} Best?</h5>
                            <table className="table table-bordered table-striped">
                                <thead >
                                    <tr>
                                        <th className="bg-danger text-white">Name</th>
                                        <th className="bg-danger text-white">Score</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {getScored && getScored?.map((x, i) => {
                                        return (<tr key={i}>
                                            <td className='text-capitalize'>
                                                {x.c_name}
                                            </td>
                                            <td>
                                                {x.c_score}
                                            </td>
                                        </tr>)
                                    })}
                                </tbody>
                            </table>
                        </div>
                        <a href={window.location.origin} onClick={createOwnQuiz} className="btn btn-warning mt-2 p-1">Create Your Own Quiz</a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FriendAttempterScore;