
import wrongSelect from '../../Assets/beep-01.mp3'
import CorrectSelect from '../../Assets/beep-02.mp3'

function QuizItemCard(currentQuestion) {
    let wrongSelectCard = new Audio(wrongSelect);
    let CorrectSelectCard = new Audio(CorrectSelect);
    const handleSound = (e) => {
        if (e) {
            wrongSelectCard.play()
        }
        else {
            CorrectSelectCard.play()
        }
    }

    return (<>
        <div className='col-6 col-sm-6 mb-3' onClick={() => currentQuestion.onCardClick(currentQuestion.value.a_id + " " + currentQuestion.value.q_id)} data-value={currentQuestion.value.a_id + " " + currentQuestion.value.q_id} >
            <div onClick={() => handleSound(currentQuestion.value.a_id === +currentQuestion.owner_a_id)} id={currentQuestion.value.a_id === +currentQuestion.owner_a_id ? 'card-active' : 'card-active-wrong'} className={currentQuestion.value.a_id === +currentQuestion.owner_a_id ? 'card-active border-radius-20 border-1-grey' : ' border-1-grey card-active-wrong border-radius-20'}>
                <div className='card-body-img '>
                    <img src={`${process.env.REACT_APP_BASE_URL}/public/${currentQuestion.value.a_thumb}`} alt={currentQuestion.value.a_text} />
                </div>
                <button type="button" className={currentQuestion.value.a_id === +currentQuestion.value?.owner_a_id ? 'card-active p-0 bg-danger btn fw-bolder p-2 text-white w-100 border-radius-0' : 'card-active-wrong bg-danger btn fw-bolder p-2 text-white w-100 p-0  border-radius-0'}
                    value={currentQuestion.value.a_id + " " + currentQuestion.value.q_id}>
                    {currentQuestion.value.a_text}
                </button>
            </div>
        </div>
    </>)
}
export default QuizItemCard;
