import React, { useContext, useRef } from 'react';
import { DareClientContext } from './LayoutClient'
import { ToastContainer, toast } from 'react-toastify';
import twentythree from '../../Assets/2023.png';

function StartCardData() {
    const inputhandleUserNameRef = useRef(null);
    const { handleUserClick } = useContext(DareClientContext);
    const handleStartCardEventFn = (e) => {
        if (!!inputhandleUserNameRef.current.value) {
            handleUserClick({ "userName": inputhandleUserNameRef.current.value })
        }
        else {
            toast("User Name is required")
        }
    };
    return (
        <div className="container">
            <div className="col-md-6 offset-md-3 mt-5">
                <div className="card mt-3 border-1-black">
                    <div className="card-body text-center">
                        <img src={twentythree} className='max-width-150' alt="challenge header img" />
                        <h2>Do Your Friends Know You?</h2>
                        <div className="input-group mb-3">
                            <span className="input-group-text"><svg style={{width: '20px'}} viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><defs></defs><title/><g data-name="Layer 7" id="Layer_7"><path className="cls-1" d="M19.75,15.67a6,6,0,1,0-7.51,0A11,11,0,0,0,5,26v1H27V26A11,11,0,0,0,19.75,15.67ZM12,11a4,4,0,1,1,4,4A4,4,0,0,1,12,11ZM7.06,25a9,9,0,0,1,17.89,0Z"/></g></svg></span>
                            <input maxLength={20} ref={inputhandleUserNameRef} type="text" className="form-control" placeholder="Username" />
                        </div>
                        <div>
                            <button className="bg-danger btn text-white border-radius-100 p-2" onClick={handleStartCardEventFn}>Start</button>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    );
}

export default StartCardData;